/* Centering the form on the page */
form {
    max-width: 600px;
    margin: 50px auto;
    padding: 30px; 
    border: 1px solid #ddd;
    border-radius: 10px;
    background-color: #ffffff; 
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1); 
}

input[type="text"], input[type="number"], textarea, select {
    width: 100%;
    padding: 12px;
    margin: 12px 0; 
    border: 1px solid #ccc;
    border-radius: 6px; 
    box-sizing: border-box;
    font-size: 16px; 
    transition: border-color 0.3s ease; 
}


input[type="text"]:hover, input[type="number"]:hover, textarea:hover, select:hover {
    border-color: #888; 
}

input[type="text"]:focus, input[type="number"]:focus, textarea:focus, select:focus {
    border-color: #4CAF50;
    outline: none; 
}


button, input[type="button"] {
    background-color: #ff8c00; 
    color: white;
    padding: 14px 22px;
    margin: 10px 0;
    border: none;
    border-radius: 6px;
    cursor: pointer;
    width: 100%;
    text-align: center;
    font-size: 18px;
    transition: background-color 0.3s ease; 
}


button:hover, input[type="button"]:hover {
    background-color: #e07b00;
}


label {
    font-weight: bold;
    display: block;
    margin-bottom: 8px;
    font-size: 16px;
    color: #333; 
}


input[type="file"] {
    margin-top: 10px;
    padding: 10px;
    font-size: 14px;
}

.inline-elements {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 12px;
}


.inline-elements > * {
    flex: 1 1;
    margin-right: 10px;
}


.inline-elements > *:last-child {
    margin-right: 0;
}


form .inline-elements input[type="file"], form .inline-elements input[type="button"] {
    width: auto;
    font-size: 14px;
}


@media (max-width: 768px) {
    form {
        padding: 20px;
    }

    .inline-elements {
        flex-direction: column;
    }

    .inline-elements > * {
        margin-right: 0;
        margin-bottom: 12px;
    }

    .inline-elements > *:last-child {
        margin-bottom: 0;
    }
}

/* General Layout */
.wrapper {
